.Vacations .allVacations {
    justify-items: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row;
    gap: 20px 0px;
    margin: 0px 5%;
}

.Vacations .vacation {
    width: 70%;
    margin-top: 30px;
    border: solid 12px #fff;
    background: #fff;
    box-shadow: 0 0 15px 0px #555;
    transition: all 1s ease;
    -o-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -webkit-transition: all 1s ease;

    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 50% 5% repeat(5, 10%);
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        " img"
        "."
        "destination"
        " description"
        " dates"
        "price"
        "followers"
}

.Vacations .destination {
    font-weight: bolder;
    font-size: large;
    grid-area: destination;
}

.Vacations img {
    height: 200px;
    width: 100%;
    grid-area: img;
}


.Vacations .description {
    grid-area: description;
    width: 100%;
    word-wrap: break-word;
    word-break: break-word;
}

.Vacations .dates {
    grid-area: dates;
}

.Vacations .following {
    cursor: pointer;
}

.Vacations .followers {
    grid-area: followers;
}

.Vacations .price {
    grid-area: price;
}