.Login > form {
  width: 30%;
  background-color: rgba(81, 209, 255, 0.486);
  text-align: left;
  margin: 0% auto 1% auto;
  border: 1px transparent solid;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
  -webkit-box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
  -moz-box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
}

.Login button {
  margin-top: 10px;
  background-color: rgb(49, 159, 255);
  padding: 10px;
}

.Login .sign-up {
  background-color: rgba(173, 255, 192, 0.591);
  width: 30%;
  border: 1px transparent solid;
  padding: 20px;
  border-radius: 10px;
  margin: 0% auto;
  box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
  -webkit-box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
  -moz-box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
}
