.Home {
    text-decoration: none;
}

.Home h1 {
    position: relative;
    right: 300px;
    top: 100px;
}

.Home button {
    margin: 10px;
    padding: 10px;
    border: 1px solid transparent;
    border-radius: 8px;
    box-shadow: 5px black 5px black;
    cursor: pointer;
    position: relative;
    left: 60%;
    width: 100px;
    font-weight: bolder;
    box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
    -webkit-box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
    -moz-box-shadow: 5px 7px 0px 0px rgba(122, 122, 122, 0.5);
}

.Home .text {
    margin-left: 50px;
    word-wrap: break-word;
    width: 400px;
    position: relative;
    top: 100px;
    left: 20%;
    margin-right: 10px;
}