.AdminVacations .allVacations {
    justify-items: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row;
    gap: 20px 0px;
    margin: 0px 5%;
}

.AdminVacations .vacation {
    width: 70%;
    position: relative;
    border: solid 12px #fff;
    background: #fff;
    box-shadow: 0 0 15px 0px #555;
    transition: all 1s ease;
    -o-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -webkit-transition: all 1s ease;
    top: 30px;

    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 50% 5% repeat(5, 10%);
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        " img"
        "actions"
        "destination"
        " description"
        " dates"
        "price"
        "followers";
}

.AdminVacations img {
    height: 150px;
    width: 150px;
    grid-area: img;
}

.AdminVacations .destination {
    font-weight: bolder;
    font-size: large;
    grid-area: destination;
}

.AdminVacations .description {
    grid-area: description;
}

.AdminVacations .dates {
    grid-area: dates;
}

.AdminVacations .followers {
    grid-area: followers;
}

.AdminVacations .price {
    grid-area: price;
}

.AdminVacations .actions {
    grid-area: actions;
}

.AdminVacations button {
    width: 40px;
    height: 40px;
    cursor: pointer;
    background-color: transparent;
    text-align: center;
}