.AddVacation>form {
    width: 300px;
    text-align: left;
    margin: 50px auto;
    border: 1px solid;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 10px 10px 10px;
}

.AddVacation input, .AddVacation select, .AddVacation button, .AddVacation textarea {
    width: 100%;
    margin-bottom: 20px;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 5px;
}

.AddVacation button {
    cursor: pointer;
}